<template>
  <div class="me-setting">
    <me-header :title="title"></me-header>
    <setting-item>
      <span></span>
      <img :src="userInfo.user_avatar" class="avatar">
    </setting-item>
    <setting-item @click.native="setPet(userInfo.user_pet)">
      <span></span>
      <span>{{userInfo.user_pet}}</span>
    </setting-item>
    <setting-item @click.native="sexClick(userInfo.user_sex)">
      <span></span>
      <span>{{userInfo.user_sex || ''}}</span>
    </setting-item>
    <setting-item @click.native="setBir(userInfo.user_birthday)">
      <span></span>
      <span>{{(userInfo.user_birthday || '') | momentFormat}}</span>
    </setting-item>
    <setting-item>
      <span></span>
      <span>{{setConst}}</span>
    </setting-item>
    <setting-item @click.native="toAddress">
      <span></span>
      <span>{{userInfo.user_address || ''}}</span>
    </setting-item>
    <div class="out-login" @click="loginOut">
      
    </div>
    <!--  -->
    <mt-actionsheet
      :actions="actions"
      v-model="sheetVisible">
    </mt-actionsheet>
    <!--  -->
    <mt-datetime-picker
      ref="picker"
      type="date"
      v-model="birthday"
      year-format="{value} "
      month-format="{value} "
      date-format="{value} "
      :startDate="startDate"
      :endDate="endDate"
      @confirm="handleBirthday"
    >
    </mt-datetime-picker>
    <!--  -->
    <div class="address" v-show="addInp">
      <div class="address-box">
        <v-distpicker
          type="mobile"
          @selected='selected'
          v-show="addInp"
        >
        </v-distpicker>
      </div>
      <div class="address-btn">
        <button @click="addInp=false"></button>
        <button @click="setAddress"></button>
      </div>
    </div>
  </div>
</template>
<script>
import MeHeader from './MeHeader'
import { mapState } from 'vuex'
import SettingItem from './SettingItem'
import { setPersonal } from './../../../api/index.js'
import moment from 'moment'
import VDistpicker from './Order/Distpicker'
import { MessageBox, Toast } from 'mint-ui'
export default {
  name: 'MeSetting',
  data () {
    return {
      title: '',
      sex: '',
      pet: '',
      birthday: new Date('1960-01-01'),
      constellation: '',
      sheetVisible: false, // 
      actions: [
        {
          name: '',
          method: this.selectSex
        },
        {
          name: '',
          method: this.selectSex
        }
      ],
      startDate: new Date('1960-01-01'), // 
      endDate: new Date(), // 
      addInp: false, // 
      mask: false,
      province: '', // 
      city: '', // 
      area: '' // 
    }
  },
  created () {
    this.$route.meta.hideBottomTabBar = true
  },
  computed: {
    ...mapState(['userInfo']),
    // 
    setConst () {
      let constellation = ''
      if (this.userInfo.user_birthday) {
        let month = moment(this.userInfo.user_birthday).format('M')
        let day = moment(this.userInfo.user_birthday).format('D')
        constellation = ''.substr(month * 2 - (day < '102223444433'.charAt(month - 1) - -19) * 2, 2) + ''
      } else {
        constellation = ''
      }
      return constellation
    }
  },
  methods: {
    sexClick (val) {
      this.sex = val
      this.sheetVisible = true
    },
    // 
    selectSex (props) {
      if (props.name === this.sex) {
        this.sheetVisible = false
      } else {
        let params = {
          sex: props.name
        }
        this.$store.dispatch('setMePersonal', params)
      }
    },
    // 
    setBir (val) {
      if (val) {
        this.birthday = new Date(val)
      }
      this.$refs.picker.open()
    },
    // 
    handleBirthday (date) {
      let params = {
        birthday: date
      }
      this.$store.dispatch('setMePersonal', params)
    },
    // 
    toAddress () {
      this.mask = true
      this.addInp = true
    },
    // 
    selected (data) {
      this.mask = false
      // this.addInp = false
      this.city = data.province.value + ' ' + data.city.value + ' ' + data.area.value
    },
    // 
    setAddress () {
      if (this.userInfo.user_address !== this.city) {
        let params = {
          city: this.city
        }
        this.$store.dispatch('setMePersonal', params)
        this.addInp = false
      } else {
        this.addInp = false
      }
    },
    // 
    setPet (val) {
      MessageBox.prompt(' ', {
        title: '',
        inputValue: val
      }).then(action => {
        if (this.userInfo.user_pet !== action.value) {
          let params = {
            pet: action.value
          }
          this.$store.dispatch('setMePersonal', params)
        }
      }).catch(calcel => {})
    },
    // 
    loginOut () {
      MessageBox.confirm('', {
        title: ''
      }).then(action => {
        this.$store.dispatch('userOut')
        Toast({
          message: '',
          iconClass: 'iconfont icon-xiugaichenggong success',
          duration: 1000
        })
        setTimeout(() => {
          this.$router.push('/home')
        }, 1000)
      }).catch(calcel => {})
    }
  },
  filters: {
    momentFormat (val) {
      return val !== '' ? moment(new Date(val)).format('YYYYMD') : val
    }
  },
  components: {
    MeHeader,
    SettingItem,
    VDistpicker
  }
}
</script>
<style lang="less" scoped>
.me-setting {
  padding-top: 1.1rem;
  position: relative;
  z-index: 99;
  .avatar {
    height: 1.4rem;
    border-radius: 50%;
  }
  .out-login {
    width: 100%;
    position: fixed;
    bottom: 0;
    height: 1rem;
    font-size: .5rem;
    text-align: center;
    line-height: 2;
    background-color: #ff0033;
    color: #fff;
    font-weight: lighter;
  }
  .address {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 555;
    background-color: rgba(0,0,0,0.3);
    .address-box {
      height: 6rem;
      overflow: hidden;
      position: absolute;
      bottom: 1rem;
      width: 100%;
      background-color: #fff;
      overflow: auto;
    }
    .address-btn {
      border-top: 1px solid #C0C0C0;
      height: 1rem;
      position: fixed;
      bottom: 0;
      display: flex;
      width: 100%;
      background-color: #fff;
      button {
        flex: 1;
        font-size: .4rem;
        color: #ff0033;
        &:first-child {
          color: #000;
          border-right: 1px solid #c0c0c0;
        }
      }
    }
  }
}
</style>



